* {
  outline: none;
}
body {
  font-family: 'Open Sans', sans-serif;
}

body {
  margin: 0;
  overflow-y: overlay;
  font-family: "Poppins Regular";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

::-webkit-scrollbar {
  width: 6px !important;
  height: 6px !important;
}

body ::-webkit-scrollbar-thumb {
  background-color: hsla(0, 0%, 100%, 0.16);
}

::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.2);
}

::-webkit-scrollbar-track {
  background: hsla(0, 0%, 100%, 0.1);
}

.btn {
  border-radius: 50px;
  padding: 14px 0;
  width: 160px;
  cursor: pointer;
  transition: 0.2s;
  font-family: "Poppins SemiBold";
}

.btn:active {
  -webkit-transform: translateY(2px);
          transform: translateY(2px);
}

.primary-btn {
  color: var(--white);
  border: 2px solid linen;
  font-size: 12px;
  background-color: #1f2235;
}
/* @media only screen and (max-width: 414px) {
  .primary-btn {
    font-size: 13px;
  }
} */

.primary-btn:hover {
  color: aliceblue;
  border: 2px solid #ff5823;
  background-color: #1f2235;
}

.highlighted-btn {
  color: var(--white);

  font-size: 12px;
  background-color: #ff5823;
}
@media only screen and (max-width: 568px) {
  .highlighted-btn {
    z-index: -900;
  }
}

.red-border {
  border: 1px solid var(--error-message) !important;
}

.fade-in {
  opacity: 0;
  -webkit-transform: translateY(80px);
          transform: translateY(80px);
  transition: all 0.8s ease;
}








:root{
  --light-blue : #04C3FF;
  --light-blue-shadow:  rgb(69 211 255 / 27%);
  --sea-green: #0ABEAD;
  --sea-green-dark: #0CBFAE;
  --sea-green-extra-dark: #09BEAD;
  --sea-green-shadow: rgba(11, 190, 173,0.4);
  --header-hamburger-background : rgba(11, 190, 173,0.8);
  --orange: #1DA1F2;
  --dark-orange: #1DA1F2;
  --orange-shadow: rgba(245, 127, 23,0.2);
  --white: #ffffff;
  --black: #333333;
  --disabled-btn: #09bead7a;
  --error-message:  #e44f4fd1;
  --toast-error: #e44f4ffa;
  --toast-shadow:  #9c9c9ce3;
  --toast-success: #0CBFAE;
  --loader-color: #0CBFAE
}
.header-container {
  display: flex;
  justify-content: center;
  height: 140px;
  color: var(--white);
}

.header-parent {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  width: 70%;
  color: var(--white);
}

.header-logo {
  font-size: 50px;
  font-family: "Poppins ExtraBold";
  color: var(--white);
}

.header-options {
  display: flex;
  align-items: center;
}

.header-option {
  font-size: 16px;
  cursor: pointer;
  font-weight: 600;
}

.header-hamburger {
  display: none;
  color: var(--white);
}

.header-option-seperator {
  margin: 0 50px 0 0;
  color: var(--white);
}

.selected-header-option {
  color:var(--dark-orange);
}

/* IPAD PRO RESPONSIVENESS */
@media only screen and (max-width: 1110px) {
  .header-parent {
    width: 80%;
  }
}

@media only screen and (max-width: 970px) {
  .header-hamburger {
    display: block;
  }

  .header-parent {
    width: 100%;
    padding: 0 40px;
    position: relative;
  }
  .header-options {
    display: flex;
    flex-direction: column;
    position: absolute;
    left: -100%;
    transition: all 0.8s ease-out;
    top: 0px;
    height: 100vh;
    width: 100%;
    justify-content: space-around;
    background-color: #1A1A1A;
    z-index: 1000;
    opacity: 0.5;
  }

  .header-option-seperator {
    margin: 0;
  }

  .selected-header-option {
    color: var(--white);
  }

  .header-option {
    color: var(--white);
  }

  .header-hamburger-bars {
    font-size: 36px;
    cursor: pointer;
    margin: 0px 10px;
    color: var(--white);
  }

  .show-hamburger-options {
    left: 0;
    opacity: 1;
  }
}
.profile-container{
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    height: 10%;
    min-height: -webkit-fit-content;
    min-height: -moz-fit-content;
    min-height: fit-content;
    background-color: 	#1A1A1A;
}
.profile-parent{
    display: flex;
    align-items: center;
    color: aliceblue;
}
.profile-details-name{
    font-size: 28px;
    font-family: 'Poppins SemiBold';
}
.profile-details-role{
display: flex;
flex-direction: column;
margin: 14px 0 24px 0;
}
.profile-details-role h1{
font-size: 30px;
font-family: "Open Sans";

}
.primary-text{
    color: (--white);
}
.highlighted-text{
    color: var(--dark-orange);
}
.profile-role-tagline{
    font-size: 19px;
    margin: 5px 0 0 0;
    align-items: center;
    font-family: "Poppins Light";
}
.btn-primary:hover, .btn-primary:focus, .btn-primary:active, .btn-primary.active, .open>.dropdown-toggle.btn-primary {
    color: #ffffff;
    margin: 5px 0 0 0;
    justify-content: center;
    background-color:#00b3db;
    border-color: #285e8e; /*set the color you want here*/
}
.profile-picture{
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 1px 0px 0px var(--white);
    border-radius: 50%;
    height: 380px;
    width: 380px;
    margin: 0 0px 0 100px;
}
.profile-picture-background{
    height: 92%;
    width: 92%;
    background-image: url(/my-app/static/media/me.eea4a048.JPG);
    background-size: cover;
    border-radius: 50%;
    background-repeat: no-repeat;
    background-position: center;
}
.profile-picture-background:hover{
    -webkit-transform: scale(1.07);
            transform: scale(1.07);
    transition: 1s ease-out;
}
.colz .colz-icon i{
    color: white;
    padding: 20px;
    align-items: center;
    justify-content: center;
    padding-left: 0px;
}
.colz .colz-icon i:hover{
    -webkit-transform: scale(1.5);
            transform: scale(1.5);

}



@media screen and (max-width:1100px){

    .profile-picture{
        margin: 0 0px 0 80px;
    }
    .profile-details-name{
        font-size: 30px;
    }
    .profile-details-role,.primary-text{
        font-size: 20px;
    }
    .profile-options .btn{
        width: 120px;
        padding: 11px 0;
        z-index: 700;
    }
    
}

@media screen and (max-width:910px){

    .profile-picture{
        height: 320px;
        width: 320px;
        margin: 0 0 0 4x;
    }
}
@media screen and (max-width:810px){

    .profile-picture{
        margin: 0 !important;
    }
    .profile-parent{
        flex-direction: column-reverse;
        margin: 40px 0 0 0;
    }
    .btn-primary:hover, .btn-primary:focus, .btn-primary:active, .btn-primary.active, .open>.dropdown-toggle.btn-primary {
        color: #ffffff;
        margin: 5px 0 0 0;
        justify-content: center;
        background-color:#00b3db;
        border-color: #285e8e; /*set the color you want here*/
    }
    .profile-details{
        margin: 25px 0 0;
    }
    .profile-options.highlighted-btn{
        margin: 0 !important;
        
    }
    .profile-options,.profile-details-role{
        text-align: center;
    }

}
@media screen and (max-width:375px){

    .profile-picture{
        height: 275px;
        width: 275px;
        margin-top: -90px;
    }
    .profile-details{
        width: 90%;
    }
    
}
.footer-container{

    height: 17.8%;
    width: 100%;
    overflow: hidden;
    display: flex;
    justify-content: center;

}
.footer-parent{
    width: 100%;
    height: 100%;
    max-width: 1920px;
}
.footer-parent img{
    max-width: 100%;
    height: 130px;

}
@media only screen and (max-width:1100px){
    .footer-parent{
        display: flex;
        align-items: flex-end;
        
    }

}
.home-container{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: 670px;
    background-color: 	#1A1A1A;
}

.heading-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin: 0 0 50px 0;
  }
  
  .screen-sub-heading {
    letter-spacing: 3px;
    margin: 8px 0 18px 0;
    font-size: 12px;
    color: var(--black);
  }
  
  .screen-heading {
    font-size: 32px;
    color: #1f2235;
    font-family: "Poppins Bold";
  }
  
  .heading-seperator {
    display: flex;
    align-items: center;
    position: relative;
    width: 180px;
    margin: 10px 0 0 0;
  }
  
  .seperator-line {
    width: 100%;
    height: 2px;
    background-color: #1f2235;
  }
  
  .seperator-blob {
    height: 10px;
    width: 100%;
    position: absolute;
    display: flex;
    justify-content: center;
  }
  
  .seperator-blob div {
    width: 35px;
    border-radius: 10px;
    background-color: var(--dark-orange);
  }
.about-me-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-family: "Open Sans";
    align-items: center;
    margin: 50px 0 0 0;
  }
  
  .about-me-parent {
    max-width: 1000px;
    font-family: "Open Sans";
    width: 70%;
  }
  
  .about-me-card {
    display: flex;
    width: 100%;
    font-family: "Open Sans";
    box-shadow: 0 0px 20px -2px #1f2235;
  }
  
  .about-me-profile {
    width: 50%;
    background-image: url(/my-app/static/media/photo.1772795f.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
  }
  
  .about-me-details {
    width: 48%;
    text-align: justify;
    padding: 30px;
  }
  
  .about-me-description {
    font-size: 12px;
    font-family: "Open Sans";
  }
  
  .about-me-highlights {
    
    margin: 80px 0;
  }
  
  .highlight-heading {
    font-size: 14px;
    margin: 0 0 5px 0;
    font-family: "Open Sans";
  }
  
  .highlight {
    display: flex;
    align-items: center;
    margin: 5px 0;
    font-size: 12px;
  }
  
  .highlight-blob {
    height: 12px;
    width: 12px;
    margin: 0 10px 0 0;
    border-radius: 50%;
    background-color:var(--dark-orange);
  }
  .btn-primary:hover, .btn-primary:focus, .btn-primary:active, .btn-primary.active, .open>.dropdown-toggle.btn-primary {
    color: #ffffff;
    margin: 5px 0 0 0;
    align-items: center;
    justify-content: center;
    font-family: "Open Sans";
    background-color:#00b3db;
    border-color: #285e8e; /*set the color you want here*/
}
  
  .about-me-options .highlighted-btn {
    margin: 0 0 0 30px;
  }
  
  /* IPAD PRO RESPONSIVENESS */
  @media only screen and (max-width: 1110px) {
    .about-me-parent {
      width: 88%;
    }
  }
  
  /* MOBILE RESPONSIVENESS */
  @media only screen and (max-width: 898px) {
    .about-me-profile {
      display: none;
    }
  
    .about-me-details {
      width: 100%;
    }
  }
  
  /* MOBILE RESPONSIVENESS */
  @media only screen and (max-width: 466px) {
    .about-me-options {
      display: flex;
      flex-direction: column;
    }
  
    .about-me-options .highlighted-btn {
      margin: 24px 0 0 0 !important;
    }
  
    .about-me-options button {
      width: 100%;
    }
  }
.resume-container {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    min-height: -webkit-fit-content;
    min-height: -moz-fit-content;
    min-height: fit-content;
    margin: 120px 0;
  }
  
  
  .resume-card {
    display: flex;
    height: 360px;
    width: 1000px;
    /* margin: 80px 0; */
  }
  
  .resume-bullets {
    box-shadow: 15px 0 9px -15px #1f2235;
    height: 100%;
    width: 320px;
  }
  
  .resume-bullet-details {
    flex-grow: 1;
    width: 600px;
    overflow: hidden;
    padding: 0 0 0 80px;
  }
  
  .experience-description {
    margin: 10px 0 0 0;
    text-align: justify;
  }
  
  .resume-description-text {
    font-size: 12px;
  }
  
  .resume-details-carousal {
    transition: -webkit-transform 1s ease-out;
    transition: transform 1s ease-out;
    transition: transform 1s ease-out, -webkit-transform 1s ease-out;
  }
  
  .bullet-container {
    display: flex;
    align-items: center;
    height: 100%;
    width: 100%;
    position: relative;
  }
  
  .bullets {
    width: 86%;
    position: relative;
    z-index: 2;
  }
  
  .bullet {
    display: flex;
    align-items: center;
    cursor: pointer;
    background-color: #1A1A1A;
    height: 40px;
    margin: 15px 0;
    padding: 0 8px;
    border-radius: 20px;
    width: 30px;
    transition: width 0.6s ease;
  }
  
  .bullet-label {
    font-size: 14px;
    white-space: nowrap;
    font-family: "Poppins SemiBold";
  }
  
  .bullet-logo {
    height: 16px;
    margin: 0 30px 0 0;
  }
  
  .bullet-icons {
    width: 34px;
    height: 100%;
    z-index: 1;
    background-color: #1A1A1A;
    position: absolute;
  }
  
  .selected-bullet {
    color: var(--white);
    width: 100%;
  }
  
  .resume-screen-container {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    height: 360px;
    width: 100%;
  }
  
  .resume-heading {
    display: flex;
    flex-direction: column;
  }
  
  .resume-main-heading {
    position: relative;
    display: flex;
    justify-content: space-between;
  }
  
  .resume-main-heading span {
    color: var(--dark-orange);
    font-size: 16px;
    font-family: "Poppins SemiBold";
  }
  
  .experience-description {
    max-width: 100%;
  }
  
  .heading-bullet {
    position: absolute;
    left: -30px;
    height: 15px;
    width: 15px;
    top: 5px;
    border-radius: 50%;
    background-color:var(--dark-orange);
  }
  
  .resume-sub-heading {
    font-size: 14px;
    font-family: "Poppins SemiBold";
    color: var(--black);
    margin: 10px 0 0 0;
  }
  
  .resume-heading-description {
    font-size: 12px;
    text-align: justify;
  }
  
  .heading-date {
    background-color:var(--dark-orange);
    padding: 4px 12px;
    font-size: 14px;
    border-radius: 14px;
    color: var(--white);
  }
  
  .skill-parent {
    display: flex;
    flex-direction: column;
    position: relative;
    width: 50%;
  }
  
  .programming-skills-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  
  .skill-percentage {
    position: relative;
    height: 15px;
    width: 70%;
    background-color: var(--dark-orange);
  }
  
  .skill-percentage .active-percentage-bar {
    height: 15px;
    transition: 0.8s ease;
    position: absolute;
    background-color: #1f2235;
  }
  
  .skill-parent span {
    font-family: "Poppins Bold";
    font-size: 16px;
    color: var(--dark-orange);
  }
  
  /* IPAD PRO RESPONSIVENESS */
  @media only screen and (max-width: 1110px) {
    .resume-card {
      width: 880px;
    }
  
    .resume-bullet-details {
      padding: 0 0 0 70px;
    }
  }
  
  /* IPAD RESPONSIVENESS */
  @media only screen and (max-width: 1023px) {
    .resume-content {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  
    .bullets {
      width: 90%;
    }
  
    .resume-card {
      height: -webkit-fit-content;
      height: -moz-fit-content;
      height: fit-content;
      display: flex;
      flex-direction: column;
      margin: 30px 0;
      align-items: center;
      width: 90%;
    }
  
    .resume-bullet-details {
      height: 360px;
      padding: 0;
      width: -webkit-fit-content;
      width: -moz-fit-content;
      width: fit-content;
    }
  
    .resume-bullets {
      margin: 30px 0;
      width: 100%;
    }
  
    .selected-bullet {
      width: 100%;
    }
    
  }

  .main_div {
    display: flex;
    text-align:center;
    position: relative;
}
.sub_div {
  position: absolute;
  bottom: 0px;
}
p {
  font-size: 13px;
  font-family: "Open Sans";
  opacity: 50%;
  display: flex;
  text-align: center;
}
  
.loading-bar div{
    background: var(--loader-color) !important;
    box-shadow: var(--loader-color) 0px 0px 10px, var(--loader-color) 0px 0px 10px !important;
    color: var(--loader-color) !important;
}
