.footer-container{

    height: 17.8%;
    width: 100%;
    overflow: hidden;
    display: flex;
    justify-content: center;

}
.footer-parent{
    width: 100%;
    height: 100%;
    max-width: 1920px;
}
.footer-parent img{
    max-width: 100%;
    height: 130px;

}
@media only screen and (max-width:1100px){
    .footer-parent{
        display: flex;
        align-items: flex-end;
        
    }

}